"use client";

import React, { useState, useEffect, useCallback } from "react";
// REMOVED: import "./viewDoc.module.css"; // No longer needed if using Tailwind directly
import {
    Edit,
    Trash2,
    Eye,
    XCircle,
    User,
    MapPin,
    Briefcase,
    Calendar,
    Phone,
    Mail,
    Stethoscope,
    GraduationCap,
    Info, // Assuming you might have an icon for department
    Syringe, // Adding this back for BloodGroup if you liked it from previous suggestion
} from "lucide-react";

// Interfaces for data structures (re-used from your DoctorRegistrationForm)
interface District {
    id: number;
    name: string;
}

interface Panchayath {
    panchayathId: number;
    panchayathName: string;
    dstId: number;
    dstName: string;
}

interface DoctorData {
    Doc_Id: string;
    district: string;
    panchayath: string;
    ward_no: string;
    DocName: string;
    address: string;
    gender: string;
    dob: string;
    age: string;
    BloodGroup: string;
    phone: string;
    email: string;
    specialization: string;
    degree: string;
    experience: string;
    licenseNumber: string;
    department: string;
    Photo: string | null; // Changed to string for URL in display
    description: string;
}

const DoctorList: React.FC = () => {
    const [doctors, setDoctors] = useState<DoctorData[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);
    const [showDetailsModal, setShowDetailsModal] = useState<boolean>(false);
    const [selectedDoctor, setSelectedDoctor] = useState<DoctorData | null>(
        null
    );
    const [deleteMessage, setDeleteMessage] = useState<string>("");
    const [deleteStatus, setDeleteStatus] = useState<"success" | "error" | "">(
        ""
    );

    // Function to fetch doctors from API
    const fetchDoctors = useCallback(async () => {
        setLoading(true);
        setError(null);
        try {
            const response = await fetch("https://localhost:7112/api/Doctors");
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const data: DoctorData[] = await response.json();

            // --- NEW & ENHANCED DEBUGGING FOR DOC_ID UNIQUENESS AND PRESENCE ---
            const docIds: { [key: string]: number } = {}; // Object to count occurrences
            let hasMissingOrNullIds = false;

            data.forEach((doc, index) => {
                if (doc.Doc_Id === undefined || doc.Doc_Id === null || doc.Doc_Id === "") {
                    console.error(`ERROR: Doctor at index ${index} has a missing/null/empty Doc_Id:`, doc);
                    hasMissingOrNullIds = true;
                } else {
                    // Convert Doc_Id to string for consistent keying, although it's already string in interface
                    const idAsString = String(doc.Doc_Id);
                    docIds[idAsString] = (docIds[idAsString] || 0) + 1;
                    if (docIds[idAsString] > 1) {
                        console.error(`ERROR: Duplicate Doc_Id found! ID: "${idAsString}" at index ${index}. Full object:`, doc);
                    }
                }
            });

            if (hasMissingOrNullIds) {
                console.error("CRITICAL: Some doctors are missing unique IDs. This is likely the cause of your React key warning.");
            }
            for (const id in docIds) {
                if (docIds[id] > 1) {
                    console.error(`CRITICAL: Doc_Id "${id}" appeared ${docIds[id]} times. All keys must be unique.`);
                }
            }
            console.log("SUCCESS: Doctors fetched and Doc_Id check complete. See above for errors.");
            // --- END NEW DEBUGGING ---

            setDoctors(data);
        } catch (err: any) {
            setError("Failed to fetch doctors: " + err.message);
            console.error("Error fetching doctors:", err);
        } finally {
            setLoading(false);
        }
    }, []);
    useEffect(() => {
        fetchDoctors();
    }, [fetchDoctors]);

    const handleViewDetails = (doctor: DoctorData) => {
        setSelectedDoctor(doctor);
        setShowDetailsModal(true);
    };

    const handleCloseDetailsModal = () => {
        setShowDetailsModal(false);
        setSelectedDoctor(null);
    };

    const handleEdit = (docId: string) => {
        console.log(`Edit doctor with ID: ${docId}`);
        // Implement navigation to an edit form or open an edit modal
        // Example: router.push(`/edit-doctor/${docId}`);
        alert(`Redirecting to edit form for Doctor ID: ${docId}`);
    };

    const handleDelete = async (docId: string) => {
        if (!window.confirm(`Are you sure you want to delete Doctor ID: ${docId}?`)) {
            return;
        }

        setDeleteMessage("");
        setDeleteStatus("");

        try {
            const response = await fetch(
                `https://localhost:7112/api/Doctors/${docId}`,
                {
                    method: "DELETE",
                }
            );

            if (response.ok) {
                setDoctors((prevDoctors) =>
                    prevDoctors.filter((doctor) => doctor.Doc_Id !== docId)
                );
                setDeleteStatus("success");
                setDeleteMessage("Doctor deleted successfully!");
            } else {
                const errorText = await response.text();
                setDeleteStatus("error");
                setDeleteMessage(`Failed to delete doctor: ${errorText}`);
                console.error("Failed to delete doctor:", response.status, errorText);
            }
        } catch (err: any) {
            setDeleteStatus("error");
            setDeleteMessage("Network error during deletion. Please try again.");
            console.error("Network error deleting doctor:", err);
        }
    };

    return (
        <div className="min-h-screen w-full bg-gradient-to-br from-purple-50 to-indigo-100 px-10 py-8">
            {/* Header */}
            <div className="bg-gradient-to-r from-indigo-800 to-purple-600 text-white p-8 text-center rounded-lg shadow-xl mb-8">
                <div className="flex justify-center mb-4">
                    <Stethoscope className="w-16 h-16 text-purple-200" />
                </div>
                <h1 className="text-4xl font-light mb-3">Our Valued Doctors</h1>
                <p className="text-xl text-purple-200">
                    A comprehensive list of healthcare professionals
                </p>
            </div>

            {/* Delete Status Message */}
            {deleteMessage && (
                <div
                    className={`mb-6 p-4 rounded-xl text-center font-semibold ${deleteStatus === "success"
                            ? "bg-green-100 text-green-800 border border-green-300"
                            : "bg-red-100 text-red-800 border border-red-300"
                        }`}
                >
                    {deleteMessage}
                </div>
            )}

            {/* Main Content */}
            <div className="bg-white p-6 rounded-2xl shadow-xl border border-gray-200">
                {loading && (
                    <div className="text-center py-8">
                        <p className="text-lg text-gray-700">Loading doctors...</p>
                        {/* You can add a spinner here */}
                        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto mt-4"></div>
                    </div>
                )}

                {error && (
                    <div className="text-center py-8 text-red-600 text-lg">
                        <p>{error}</p>
                    </div>
                )}

                {!loading && !error && doctors.length === 0 && (
                    <div className="text-center py-8 text-gray-600 text-lg">
                        <p>No doctors registered yet.</p>
                    </div>
                )}

                {!loading && !error && doctors.length > 0 && (
                    <div className="overflow-x-auto">
                        <table className="min-w-full divide-y divide-gray-200">
                            <thead className="bg-gray-50">
                                <tr>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Doctor ID
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Doctor Name
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Degree
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Specialization
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Phone Number
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider"
                                    >
                                        Actions
                                    </th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-gray-200">
                                {doctors.map((doctor) => (
                                    <tr key={doctor.Doc_Id} className="hover:bg-gray-50">
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                                            {doctor.Doc_Id}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap">
                                            <button
                                                onClick={() => handleViewDetails(doctor)}
                                                className="text-blue-600 hover:text-blue-900 font-semibold flex items-center"
                                            >
                                                <Eye className="w-4 h-4 mr-1" />
                                                {doctor.DocName}
                                            </button>
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                            {doctor.degree}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                            {doctor.specialization}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                            {doctor.phone}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-center text-sm font-medium">
                                            <button
                                                onClick={() => handleEdit(doctor.Doc_Id)}
                                                className="text-indigo-600 hover:text-indigo-900 mr-4 inline-flex items-center"
                                                title="Edit Doctor"
                                            >
                                                <Edit className="w-5 h-5" />
                                            </button>
                                            <button
                                                onClick={() => handleDelete(doctor.Doc_Id)}
                                                className="text-red-600 hover:text-red-900 inline-flex items-center"
                                                title="Delete Doctor"
                                            >
                                                <Trash2 className="w-5 h-5" />
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>

            {/* Doctor Details Modal */}
            {showDetailsModal && selectedDoctor && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-3xl shadow-2xl w-full max-w-2xl transform transition-all scale-100 opacity-100 relative p-8">
                        <button
                            onClick={handleCloseDetailsModal}
                            className="absolute top-4 right-4 text-gray-500 hover:text-gray-700 transition-colors"
                            aria-label="Close"
                        >
                            <XCircle className="w-8 h-8" />
                        </button>

                        <div className="text-center mb-6">
                            {selectedDoctor.Photo ? (
                                <div className="w-32 h-32 rounded-full overflow-hidden mx-auto mb-4 border-4 border-blue-400 shadow-lg">
                                    <img
                                        src={selectedDoctor.Photo}
                                        alt={selectedDoctor.DocName}
                                        className="w-full h-full object-cover"
                                    />
                                </div>
                            ) : (
                                <div className="w-32 h-32 rounded-full bg-blue-100 flex items-center justify-center mx-auto mb-4 border-4 border-blue-400 shadow-lg">
                                    <User className="w-16 h-16 text-blue-500" />
                                </div>
                            )}
                            <h2 className="text-3xl font-bold text-blue-800 mb-2">
                                {selectedDoctor.DocName}
                            </h2>
                            <p className="text-lg text-gray-600 font-medium">
                                {selectedDoctor.specialization} - {selectedDoctor.department}
                            </p>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-x-8 gap-y-4 text-gray-700">
                            <div className="flex items-center">
                                <Stethoscope className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Doctor ID:</span>{" "}
                                {selectedDoctor.Doc_Id}
                            </div>
                            <div className="flex items-center">
                                <GraduationCap className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Degree:</span>{" "}
                                {selectedDoctor.degree}
                            </div>
                            <div className="flex items-center">
                                <Briefcase className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Experience:</span>{" "}
                                {selectedDoctor.experience}
                            </div>
                            <div className="flex items-center">
                                <Info className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">License No:</span>{" "}
                                {selectedDoctor.licenseNumber}
                            </div>
                            <div className="flex items-center">
                                <Calendar className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">DOB:</span>{" "}
                                {new Date(selectedDoctor.dob).toLocaleDateString()} (
                                {selectedDoctor.age})
                            </div>
                            <div className="flex items-center">
                                <User className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Gender:</span>{" "}
                                {selectedDoctor.gender}
                            </div>
                            <div className="flex items-center">
                                <Mail className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Email:</span>{" "}
                                {selectedDoctor.email}
                            </div>
                            <div className="flex items-center">
                                <Phone className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Phone:</span>{" "}
                                {selectedDoctor.phone}
                            </div>
                            <div className="flex items-center md:col-span-2">
                                <MapPin className="w-5 h-5 text-blue-500 mr-3" />
                                <span className="font-semibold">Address:</span>{" "}
                                {selectedDoctor.address}, {selectedDoctor.panchayath},{" "}
                                {selectedDoctor.district}, Ward No: {selectedDoctor.ward_no}
                            </div>
                            {selectedDoctor.BloodGroup && (
                                <div className="flex items-center md:col-span-2">
                                    <Syringe className="w-5 h-5 text-blue-500 mr-3" />{" "}
                                    {/* Using Syringe icon for Blood Group */}
                                    <span className="font-semibold">Blood Group:</span>{" "}
                                    {selectedDoctor.BloodGroup}
                                </div>
                            )}
                            {selectedDoctor.description && (
                                <div className="md:col-span-2 mt-4 p-3 bg-blue-50 rounded-lg border border-blue-200">
                                    <p className="font-semibold text-blue-800 mb-2">
                                        Description:
                                    </p>
                                    <p className="text-sm italic">{selectedDoctor.description}</p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default DoctorList;